const Swal = require("sweetalert2");

export interface AlertOptions {
    title?: string; //  
    text: string; //  
    icon?: 'error'|'success'|'warning'|'info'|'question';
    type?: 'danger'|'success'|'warning'|'info';
    closeTime?: number; //  
    closeTimeDisplay?: boolean; //   
    onHide?: () => {} //   callback
}

export interface AlertLoadingOptions {
    title?: string;
    text?: string;
    icon?: 'error'|'success'|'warning'|'info';
}

export interface AlertConfirmOptions {
    title?: string; //  
    text: string; //  
    icon?: 'error'|'success'|'warning'|'info'|'question';
    confirmText?: string; //  
    cancelText?: string; //  
    closeTime?: number; //  
    closeTimeDisplay?: boolean; //   
    onHide?: () => {} //   callback
}

export class Alert {
    static info(options: AlertOptions){
        options = Object.assign({}, {
            type: 'info',
            icon: 'info'
        }, options);

        Alert.alert(options);
    }

    static warning(options: AlertOptions){
        options = Object.assign({}, {
            type: 'warning',
            icon: 'warning'
        }, options);

        Alert.alert(options);
    }

    static success(options: AlertOptions){
        options = Object.assign({}, {
            type: 'success',
            icon: 'success'
        }, options);

        Alert.alert(options);
    }

    static danger(options: AlertOptions){
        options = Object.assign({}, {
            type: 'danger',
            icon: 'error'
        }, options);

        Alert.alert(options);
    }

    static alert(options: AlertOptions){
        //   
        let timerInterval: NodeJS.Timer;
        let time = options.closeTime || 5;

        let timeContext = '';
        if(options.closeTimeDisplay){
            timeContext = `<br>[ <strong>${time}</strong>   . ]`;
        }

        Swal.fire({
            icon: options.icon || 'info',
            title: options.title || '',
            html: options.text + timeContext,
            timer: time * 1000,
            willOpen: () => {
                if (options.closeTimeDisplay) {
                    timerInterval = setInterval(() => {
                        const section = Swal.getContainer();
                        if (section !== null) {
                            const ctx = section.querySelector('strong');
                            if(ctx){
                                // @ts-ignore
                                ctx.textContent = String(Math.ceil(Swal.getTimerLeft() / 1000));
                            }
                        }
                    }, 1000);
                }
            },
            willClose: () => {
                if (timerInterval !== undefined) {
                    clearInterval(timerInterval);
                }
            },
            customClass: {
                confirmButton: `btn btn-${options.type || 'info'}`,
                cancelButton: 'btn btn-secondary'
            },
        }).then(options.onHide);
    }

    static loading(options: AlertLoadingOptions){
        Swal.fire({
            title: options.title,
            html: options.text || '  .<br> !',
            width: '25rem',
            icon: options.icon,
            allowOutsideClick: false,
            showCancelButton: false,
            showConfirmButton: false,
            backdrop: 'rgba(0, 0, 0, 0.6)'
        });
    }

    static confirm(options: AlertConfirmOptions){
        //   
        let timerInterval: NodeJS.Timer;
        let time = options.closeTime || 5;

        let timeContext = '';
        if(options.closeTimeDisplay){
            timeContext = `<br>[ <strong>${time}</strong>   . ]`;
        }

        Swal.fire({
            title: options.title || '',
            html: options.text + timeContext,
            icon: options.icon || 'info',
            showCancelButton: true,
            confirmButtonText: options.confirmText || '',
            cancelButtonText: options.cancelText || '',
            customClass: {
                confirmButton: 'btn btn-info',
                cancelButton: 'btn btn-secondary'
            },
            timer: time,
            willOpen: () => {
                if (options.closeTimeDisplay) {
                    timerInterval = setInterval(() => {
                        const section = Swal.getContainer();
                        if (section !== null) {
                            const ctx = section.querySelector('strong');
                            if(ctx){
                                // @ts-ignore
                                ctx.textContent = String(Math.ceil(Swal.getTimerLeft() / 1000));
                            }
                        }
                    }, 1000);
                }
            },
            willClose: () => {
                if (timerInterval !== undefined) {
                    clearInterval(timerInterval);
                }
            },
        }).then(options.onHide);
    }
}

export default Alert;